@mixin font($font_family, $font-size, $font-weight, $line-height, $letter-spacing: normal, $text-transform: none) {
    font-family: $font_family;
    font-size: $font-size;
    font-weight: $font-weight;
    line-height: $line-height;
    letter-spacing: $letter-spacing;
    text-transform: $text-transform;
}

@mixin media-hover-start {
    @media (min-width: $hover-start) {
        @content;
    }
}

@mixin media-tablet {
    @media (max-width: $tablet) {
        @content;
    }
}

@mixin media-mobile {
    @media (max-width: $mobile) {
        @content;
    }
}